<template>
  <div class="join-us">
    <div class="pd-10-bottom">
      <!-- <h2 class="title">{{ internationalDict.joinUs }}</h2> -->
    </div>
    <div class="container">
      <el-tabs v-model="activeName">
        <el-tab-pane class="occupation-card-group" :style="postColumns">
          <span slot="label">
            <i class="el-icon-fork-spoon"></i> 餐飲前線
          </span>
          <div v-for="(item, index) of food" :key="index" @click="showPreviewImg(item)">
            <img :src="item.img" class="occupation-item" :alt="item.alt"/>
          </div>
        </el-tab-pane>
        <el-tab-pane class="occupation-card-group" :style="postColumns">
          <span slot="label">
            <i class="el-icon-box"></i> 倉務
          </span>
          <div v-for="(item, index) of storage" :key="index" @click="showPreviewImg(item)">
            <img :src="item.img" class="occupation-item" :alt="item.alt"/>
          </div>
        </el-tab-pane>
        <el-tab-pane class="occupation-card-group" :style="postColumns">
          <span slot="label">
            <i class="el-icon-office-building"></i> 寫字樓
          </span>
          <div v-for="(item, index) of office" :key="index" @click="showPreviewImg(item)">
            <img :src="item.img" class="occupation-item" :alt="item.alt"/>
          </div>
        </el-tab-pane>
        <el-tab-pane class="occupation-card-group" :style="postColumns">
          <span slot="label">
            <i class="el-icon-office-building"></i> 實習生計劃
          </span>
          <div v-for="(item, index) of inter" :key="index" @click="showPreviewImg(item)">
            <img :src="item.img" class="occupation-item" :alt="item.alt"/>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>

    <div class="img-preview" v-show="previewShow" @click="previewShow = false">
      <img :src="currentImgLink.img" alt="preview">
    </div>

    <div v-if="windowSize === 'big'" class="hotline-box">
      <div class="hotline-container">
        <h3>招聘專線</h3>
        <div>
          <p>WHATSAPP</p>
          <p>5441 3133</p>
          <p>線上初步面試</p>
        </div>
        <img src="@/assets/jobHotLine.png" class="hotline-QRcode" />
      </div>
    </div>
    <div v-else>
      <div class="hotline-box-moblie">
        <a href="https://api.whatsapp.com/send/?phone=85254413133&text">
          <el-button round type="primary">WhatsApp招聘專線</el-button>
        </a>
      </div>
    </div>

  </div>
</template>
<script>
export default {
  name: "join-us",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      activeName: "",
      currentImgLink:"",
      previewShow:false,
      food:[],
      storage:[],
      office:[],
      inter:[]
    };
  },
  computed: {
    internationalDict() {
      return this.$store.getters.getInternationalDict;
    },
    windowSize() {
      return this.$store.getters.getWindowSize;
    },
    postColumns(){
      return this.windowSize === 'big' ? {'grid-template-columns': '1fr 1fr 1fr'} : this.windowSize === 'small' ? {'grid-template-columns': '1fr 1fr'} : {'grid-template-columns': '1fr'}
    }
  },
  watch: {},
  mounted() {
    this.getJoinUSList()
  },
  methods: {
    // dutyFomatter(context) {
    //   let res = '<span class="label">職責:</span>';
    //   let arr = context.split("|");
    //   res += `<span>${arr[0]}</span>`;
    //   if (arr.length < 2) {
    //     return res;
    //   }
    //   res += "<ul>";
    //   for (let i = 1; i < arr.length; i++) {
    //     res += `<li>${arr[i]}</li>`;
    //   }
    //   return (res += "</ul>");
    // },
    getJoinUSList() {
         // https://docs.google.com/spreadsheets/d/1p1qXDGqea-uKwm-cdDKUDD1QQluKb61COIffsd7x-mQ/edit#gid=0
         const sheetsId = "1p1qXDGqea-uKwm-cdDKUDD1QQluKb61COIffsd7x-mQ"
         const sheet = 'sheet1!A1:E10'
         const apiKey = process.env.VUE_APP_GOOGLEKEY
         const baseUrl = "https://drive.google.com/uc?export=view&id="
         fetch(`https://sheets.googleapis.com/v4/spreadsheets/${sheetsId}/values/${sheet}?key=${apiKey}`)
            .then(response => {
               return response.json()
            })
            .then(res => {
               if (!res.values.length) return;
               const header = res.values[0]
               const content = res.values.map(item => {
                  let value = {}
                  header.forEach((cell, index) => {
                     value[cell] = item[index]
                  })
                  value.img = baseUrl + value.id
                  return value
               }).slice(1)
               content.forEach(item => {
                switch(item.type){
                  case '1':
                    this.food.push(item)
                    break
                  case '2':
                    this.storage.push(item)
                    break
                  case '3':
                    this.office.push(item)
                    break
                  case '4':
                    this.inter.push(item)
                    break
                }
               })
            }).catch(err => {
               console.error(err)
            })
      },
    showPreviewImg(item){
      if(this.windowSize !== 'big')return;
      this.currentImgLink = item
      this.previewShow = true
    }
  }
};
</script>
<style>
.join-us{
  height: calc(100vh - 110px);
}

.fw-bold {
  color: var(--color-primary);
  font-weight: bold;
}
.label {
  font-weight: 700;
}
/* occupation */
.occupation-card-group {
  display: grid;
  gap: 1em;
  grid-template-columns: 1fr 1fr 1fr;
}
.occupation-card {
  display: grid;
  gap: 1rem;
  grid-template-columns: min(10%, 5rem) 1fr;
}
.occupation-item {
  width: 100%;
  object-fit: fill;
  columns: 1;
}
/* hotline-box */
.hotline-box {
  position: fixed;
  width: 140px;
  bottom: 5em;
  right: 1em;
  border-radius: 10px;
  background-color: var(--color-primary);
  padding: 0.5em;
}
.hotline-box-moblie {
  position: fixed;
  bottom: 5em;
  right: 1em;
}
.hotline-container {
  display: flex;
  flex-direction: column;
  gap: 1em;
  color: aliceblue;
  justify-content: center;
  text-align: center;
}
.hotline-QRcode {
  transform: scale(0.8);
}
.img-preview{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2000;
  background-color: rgba(0,0,0,.7);
  width: 100vw;
  height: 100vh;
}
.img-preview > img{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  height: 90vh;
  background-size: contain;
}
</style>
